@import url(https://fonts.googleapis.com/css?family=Khula&display=swap);
.todos-collection{width:100%;margin-bottom:20px}.todos-collection .todo-item{width:100%;display:flex;justify-content:space-between;align-items:center;padding:30px 20px 10px 10px;border-bottom:1px solid rgba(128,0,128,0.4);font-family:Roboto, sans-serif;font-weight:100;font-size:0.8em;color:#333333}@media (max-width: 599px){.todos-collection .todo-item{flex-direction:column}.todos-collection .todo-item.titles{display:none}.todos-collection .todo-item.total{flex-direction:row}.todos-collection .todo-item>div{width:100% !important;margin-bottom:5px}.todos-collection .todo-item>div span{display:inline-block !important;margin-right:10px}}@media print{.todos-collection .todo-item{flex-direction:row}.todos-collection .todo-item.titles{display:flex}.todos-collection .todo-item>div:nth-child(1){width:60% !important}.todos-collection .todo-item>div:nth-child(2){width:20% !important}.todos-collection .todo-item>div:nth-child(3){width:20% !important}.todos-collection .todo-item>div span{display:none !important}}.todos-collection .todo-item>div span{display:none}.todos-collection .todo-item>div:nth-child(1){width:60%}.todos-collection .todo-item>div:nth-child(2){width:20%}.todos-collection .todo-item>div:nth-child(3){width:20%}

.input-div{height:50px;margin:0 5px}.input-div .input{position:relative;padding-top:10px}.input-div .input::before{position:absolute;bottom:0;width:100%;content:'';height:1px;background:rgba(128,0,128,0.4)}.input-div .input .place-holder{position:absolute;left:0px;bottom:0px;font-size:0.9em;transition:all 0.3s ease-in-out;pointer-events:none}.input-div .input>span{display:block;position:absolute;bottom:0;background:purple;width:100%;height:2px;transition:all 0.4s ease;-webkit-transform-origin:center;transform-origin:center;-webkit-transform:scale(0);transform:scale(0)}.input-div .input .input-type{border:none;background:transparent;width:100%;font-size:0.8em;padding:0px 0}.input-div .input .input-type:focus+.place-holder{bottom:calc(70%);font-size:0.8em}.input-div .input .input-type:focus+.place-holder+span{-webkit-transform:scale(1);transform:scale(1)}.input-div .input .typing+.place-holder{bottom:calc(70%);font-size:0.8em}.input-div .input .typing+.place-holder+span{-webkit-transform:scale(1);transform:scale(1)}

.altra{width:100%}@media print{.altra{display:none}}.altra form{display:flex;flex-direction:column;justify-content:center}.altra form .inputz{display:flex}@media (max-width: 599px){.altra form .inputz{flex-direction:column;width:100%}}.altra form button{width:50%;border:none;color:white;background:purple;margin:20px auto 0;padding:5px 0;border-radius:10px;font-size:0.8em}

*,*:before,*:after{box-sizing:border-box;padding:0;margin:0;outline:none;-webkit-tap-highlight-color:transparent}body{background-color:whitesmoke;font-family:'Khula', sans-serif}.todo-list{background:white;margin:auto;width:90%;max-width:500px;display:flex;flex-direction:column;align-items:center;border:1px solid #efefef;box-shadow:0 1px 1px rgba(0,0,0,0.15),0 10px 0 -5px #eee,0 10px 1px -4px rgba(0,0,0,0.15),0 20px 0 -10px #eee,0 20px 1px -9px rgba(0,0,0,0.15);padding:30px;margin-top:20px}@media print{.todo-list{width:500px}}.print_button{display:flex;margin:50px auto 0;background:none;border:none;color:purple;font-weight:bold}@media print{.print_button{display:none}}.blue-text{color:purple}

